import React from 'react'
import { Link } from 'react-router-dom'
import { Typography , Card, CardContent, CardMedia } from '@mui/material'

import { CheckCircle } from '@mui/icons-material'
import { demoThumbnailUrl, demoVideoTitle, demoChannelUrl, demoChannelTitle, demoVideoUrl } from '../Utils/Contants'


const VideoCard = ({ item : { id : { videoId }, snippet}, statistics}) => {
    


    return (
        <Card sx = {{width: {xs : "100%" , sm : "358px", md : "320px"}, boxShadow: "none", borderRadius: '0'}}>

            <Link to = {videoId ? `/video/${videoId}` : demoVideoUrl}>

            <CardMedia image = {snippet?.thumbnails?.high?.url} 
            alt = {snippet?.title}
            sx = {{ width : {xs : "100%" , sm : "358px", }, height : 180}}
            
            />
            
            </Link>

            

            <CardContent sx = {{backgroundColor: "#1e1e1e", height: "106px"}}>
            <Link to = {videoId ? `/watch?v=${videoId}` : demoVideoUrl}>
                <Typography varient = "subtitle1"  fontWeight="bold" color= "white">
                    {snippet?.title.slice(0, 60) || demoVideoTitle.slice(0, 60)}
                </Typography>
                
                </Link>

                <Link to = {snippet?.channelId ? `/channel/${snippet?.channelId}` : demoChannelUrl}>
                <Typography varient = "subtitle2"  fontWeight="bold" color= "gray">
                    {snippet?.channelTitle || demoChannelTitle}
                    <CheckCircle sx={{fontSize: 12, color:'gray', ml : "5px"}}/>
                </Typography>
                
                </Link>

            </CardContent>
        </Card>
    )

}

export default VideoCard